import React from 'react';
import SearchBar from './SearchBar';
import axios from 'axios';

class App extends React.Component{
  state = { location: null,
            weather: null, 
            temp: null, 
            speed: null, 
            humidity: null, 
            feels_like: null};

	componentDidMount(){
		this.onTermSubmit('san jose');
	}

	onTermSubmit = async (term) => {
    console.log(term);
    
		const response = await axios.get('https://weather-westcliff.herokuapp.com/testAPI',{
			params: {
				location: term
			}
		});
    console.log(response);
    
		this.setState({ 
      location: term,
      weather: response.data.weather, 
      temp: response.data.temp, 
      speed: response.data.speed, 
      humidity: response.data.humidity, 
      feels_like: response.data.feels_like
    });
    
	};
/*
	onVideoSelect = (video) => {
		this.setState({
			selectedVideo: video,
		});
	}*/

	render() {
		return (
			<div className="ui container">
				<SearchBar onFormSubmit={this.onTermSubmit}/>
				<div className="ui grid">
					<div className="ui row">
						<div className="eleven wide column">
              <h1>{this.state.location}</h1>
              <div>Weather: {this.state.weather}</div>
              <div>Tempreture: {this.state.temp}</div>
              <div>Speed: {this.state.speed}</div>
              <div>Humidity: {this.state.humidity}</div>
              <div>Feels like: {this.state.feels_like}</div>
						</div>
						<div className="five wide column">
							<div></div>
						</div>
					</div>
				</div>
			</div>
			);
	}
}

export default App;
